import { graphql } from 'gatsby'
import PostsPage from '../containers/Posts'

export default PostsPage

export const pageQuery = graphql`
  query PostsPageQuery(
    $paginatePostsPage: Boolean!
    $skip: Int
    $limit: Int
    $includeExcerpt: Boolean!
    $includeTimeToRead: Boolean!
    $imageQuality: Int!
  ) {
    featuredPosts: allArticle(
      filter: {
        private: { ne: true }
        draft: { ne: true }
        featured: { eq: true }
        category: {
          id: {
            in: [
              "7c0c6ff9-07a8-5bde-a579-7d42fc0874fa"
              "67152c1a-551d-52b2-a8f1-c6c5f36c2fc4"
              "4f4c6676-555d-5334-b7ef-884b834eac21"
            ]
          }
        }
      }
      sort: { fields: [date], order: DESC }
      limit: 10
    ) {
      nodes {
        ...ArticlePreview
        ...ArticleThumbnailFeatured
      }
    }

    recentPosts: allArticle(
      filter: {
        private: { ne: true }
        draft: { ne: true }
        category: {
          id: {
            in: [
              "7c0c6ff9-07a8-5bde-a579-7d42fc0874fa"
              "67152c1a-551d-52b2-a8f1-c6c5f36c2fc4"
              "4f4c6676-555d-5334-b7ef-884b834eac21"
            ]
          }
        }
      }
      sort: { fields: [date], order: DESC }
      limit: 6
    ) {
      nodes {
        ...ArticlePreview
        ...ArticleThumbnailRegular
      }
    }

    posts: allArticle(
      filter: {
        private: { ne: true }
        draft: { ne: true }
        category: {
          id: {
            in: [
              "7c0c6ff9-07a8-5bde-a579-7d42fc0874fa"
              "67152c1a-551d-52b2-a8f1-c6c5f36c2fc4"
              "4f4c6676-555d-5334-b7ef-884b834eac21"
              "753f6c71-e42d-5e51-b3a0-fe00c5d91bda"
              "5a2fdb42-b194-53d7-80ca-8e4e55b67d3d"
              "f44d9003-e424-55b1-9584-a3c00ee316c6"
            ]
          }
        }
      }
      sort: { fields: [date], order: DESC }
      limit: 1000
    ) @skip(if: $paginatePostsPage) {
      group(field: category___name, limit: 10) {
        categoryName: fieldValue
        nodes {
          ...ArticlePreview
          ...ArticleThumbnailRegular
        }
      }
    }

    paginatedPosts: allArticle(
      filter: {
        private: { ne: true }
        draft: { ne: true }
        category: {
          id: {
            in: [
              "7c0c6ff9-07a8-5bde-a579-7d42fc0874fa"
              "67152c1a-551d-52b2-a8f1-c6c5f36c2fc4"
              "4f4c6676-555d-5334-b7ef-884b834eac21"
            ]
          }
        }
      }
      sort: { fields: [date], order: DESC }
      limit: $limit
      skip: $skip
    ) @include(if: $paginatePostsPage) {
      nodes {
        ...ArticlePreview
        ...ArticleThumbnailRegular
      }
      ...ArticlePagination
    }
  }
`
