import React from 'react'
import { Layout, Stack, Hero, Main, Sidebar, Banner, Wow } from '@layout'
import CardList from '@components/CardList'
import Divider from '@components/Divider'
import Seo from '@widgets/Seo'
import ScrollTop from '@widgets/ScrollTop/ScrollTop'
import { useBlogCategoriesKanash } from '@helpers-blog'
import Categories from '@widgets/Categories'

const Posts = ({
  data: { posts = {}, featuredPosts = {}, recentPosts = {} },
  ...props
}) => {
  const { pageContext: { services = {} } = {} } = props
  const categories = useBlogCategoriesKanash()

  return (
    <Layout {...props}>
      <ScrollTop showBelow={250} />
      <Seo title='الصفحة الرئيسية' /> <Seo title='Home' />
      <Divider />
      <Hero>
        <Categories categories={categories} variant='horizontal' omitTitle />
      </Hero>
      <Divider />
      <Hero sx={{ position: `relative` }}>
        <CardList
          nodes={featuredPosts.nodes}
          limit={1}
          columns={[1]}
          variant='horizontal-cover-hero'
          loading='eager'
        />
      </Hero>
      <Divider />
      <Hero>
        <CardList
          nodes={featuredPosts.nodes}
          limit={2}
          skip={1}
          columns={[1, 1, 2]}
          variant='horizontal'
          loading='eager'
        />
      </Hero>
      <Hero>
        <Divider />
        <Banner />
        <Divider />
      </Hero>
      <Divider />
      <Hero wide>
        {posts.group.length &&
          posts.group.map((group, index) => (
            <React.Fragment key={`${group.categoryName}.list`}>
              {index === 1 ? (
                <Stack
                  title={group.categoryName}
                  titleLink={group.nodes[0].category.slug}
                >
                  <Main>
                    <CardList
                      nodes={group.nodes}
                      limit={1}
                      variant={['horizontal-md', 'horizontal']}
                    />
                  </Main>
                  <Sidebar sx={{ pl: 3, display: [null, `flex`] }}>
                    <Wow />
                  </Sidebar>
                </Stack>
              ) : (
                <Stack
                  effectProps={{ effect: 'fadeInLeft' }}
                  title={group.categoryName}
                  titleLink={group.nodes[0].category.slug}
                >
                  <Main>
                    <CardList
                      nodes={group.nodes}
                      limit={1}
                      variant='horizontal-cover-hero'
                    />
                    <Divider space={2} />
                    <CardList
                      nodes={group.nodes}
                      limit={2}
                      skip={2}
                      columns={[1, 1, 2]}
                      variant='horizontal'
                      loading='eager'
                      // omitMedia
                      omitCategory
                    />
                    <Divider space={2} />
                    <CardList
                      nodes={group.nodes}
                      limit={2}
                      skip={4}
                      columns={[1, 2]}
                      variant={['horizontal-md', 'horizontal-aside']}
                      mediaType='icon'
                      omitCategory
                    />
                  </Main>
                </Stack>
              )}
              {index !== posts.group.length - 1 && <Divider />}
            </React.Fragment>
          ))}
      </Hero>
      <Divider space={3} />
    </Layout>
  )
}

export default Posts
